import React from 'react';
import { Row, Col, Card, Table, } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getCustomers, deleteCustomers } from '../../api/sales/customer';
import CustomersFilter from '../../App/components/SearchFilters/CustomersFilter';
import { getCustomersSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getStartEnd } from '../../helpers/getStartEnd';

const Customers = (props) => {
    const [customersArr, setCustomersArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, customerId: null })
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        getCustomers({
            "action": "get",
            "filter": "none",
            ...getCustomersSearchFilterObject(props),
            ...getStartEnd(props)
        }, controller.signal)
            .then((res) => {
                if (res.json.status) {
                    console.log(res.json.customers)
                    setCustomersArr(res.json.customers);
                    setInstanceCount(res.json.count)
                    setLoading(false);
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });

        return () => controller.abort();
    }, []);



    const _triggerDeleteModal = (id) => {
        setDeleteData({ showModal: true, customerId: id });
    }

    const _delete = (idToBeDeleted) => {
        deleteCustomers({ action: 'delete', customers_id: [idToBeDeleted] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deletedCustomer = customersArr.find(item => item.id === idToBeDeleted);
                    let tempArr = customersArr;
                    tempArr.splice(tempArr.indexOf(deletedCustomer), 1);
                    await setCustomersArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, customerId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Customer deleted successfully!', background: '#dffff0' })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });
    }


    const CustomersList = customersArr.map((cust, index) => {
        return (
            <tr key={index}>
                <th scope="row">{cust.id}</th>
                <td
                    onClick={() => {
                        props.history.push('/customer/' + cust.id)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{cust.first_name} {cust.last_name}</td>
                <td>{cust.category_str}</td>
                <td>{cust.email}</td>
                <td>{cust.phone1}</td>
                <td>{cust.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/customer/update/' + cust.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(cust.id)}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Customers</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`customer/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <CustomersFilter />
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Category</th>
                                        <th>Email</th>
                                        <th>Phone No.</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {CustomersList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(customersArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >


                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.customerId)}
                    />


                </Col>
            </Row>
        </Aux>
    );
}

export default Customers;